.header {
	height: 100vh;
	padding-top: 7rem;
	overflow: hidden;
}

.header__container {
	text-align: center;
	height: 100%;
	position: relative;
}

.cta {
	margin-top: 2.5rem;
	display: flex;
	gap: 1.2rem;
	justify-content: center;
}

.header__socials {
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 0.8rem;
	position: absolute;
	left: 0;
	bottom: -30rem;
	
	&::after {
		content: "";
		width: 1px;
		height: 2rem;
		background: #4b5bff;
	}
}

.me {
	background: #4b5bff;
	width: 22rem;
	height: 30rem;
	position: absolute;
	left: calc(50% - 11rem);
	margin-top: 4rem;
	border-radius: 12rem 12rem 0 0;
	overflow: hidden;
	padding: 5rem 1.5rem 1.5rem 1.5rem; 
}

.scroll__down {
	position: absolute;
	right: -2.3rem;
	bottom: -30rem;
	transform: rotate(90deg);
	font-weight: 300;
	font-size: 0.9rem;
}

/* ==================== MEDIA (MEDIUM DEVICES) ========================= */
@media screen and (max-width: 1024px) {
	.header {
		padding-top: 3rem;
	}
}

/* ==================== MEDIA (SMALL DEVICES) ========================= */
@media screen and (max-width: 600px) {
	.header {
		padding-top: 3rem;
		height: 100vh;
	}
	.header__socials, .scroll__down {
		display: none;
	}
}