.testimonials__container {
	width: 60%;
	margin: 0 auto;
}

.client__avatar {
	width: 4rem;
	overflow: hidden;
	border-radius: 50%;
	margin: 0 auto 1rem;
	border: 0.4rem solid var(--color-primary-variant);
}

.testimonials {
	background: var(--color-bg-variant);
	text-align: center;
	padding: 2rem;
	border-radius: 2rem;
	user-select: none;
}

.client__review {
	color: var(--color-light);
	font-weight: 300;
	display: block;
	width: 80%;
	margin: 0.8rem auto 0;
}

/* ==================== MEDIA (MEDIUM DEVICES) ========================= */
@media screen and (max-width: 1024px) {
	.testimonials__container {
		width: 80%;
	}
}

/* ==================== MEDIA (SMALL DEVICES) ========================= */
@media screen and (max-width: 600px) {
	.testimonials__container {
		width: var(--container-width-sm);
	}
	.client__review	 {
		width: var(--container-width-sm);
	}
}