.about__container {
	display: grid;
	grid-template-columns: 35% 65%;
	gap: 15%;
}

.about__me {
	width: 100%;
	border-radius: 2rem;
	background: var(--color-primary);
	display: grid;
	place-items: center;
}

.about__me__image {
	border-radius: 2rem;
	overflow: hidden;
	transform: rotate(10deg);
	transition: transform .5s ease;

	&:hover {
		transform: rotate(0);
	}
}

.about__cards {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	gap: 1.5rem;
}


.about__card {
	background: var(--color-primary-variant);
	border: 1px solid transparent;
	border-radius: 1rem;
	padding: 2rem;
	text-align: center;
	transition: .2s ease;

	&:hover {
		background: transparent;
		border-color: var(--color-primary-variant);
	}

	h5 {
		font-size: 0.95rem;
	}

	small	{
		font-size: 0.6rem;
		color: var(--color-light);
	}
}

.about__icon {
	color: var(--color-primary);
}

.about__content p {
	margin: 2rem 0 2.6rem;
	color: var(--color-light);
}

/* ==================== MEDIA (MEDIUM DEVICES) ========================= */
@media screen and (max-width: 1024px) {
	.about__container {
		grid-template-columns: 1fr;
		gap: 0;
	}
	.about__me {
		width: 50%;
		margin: 2rem auto 4rem;
	}
	.about__content p {
		margin: 1rem 0 1.5rem;
	}
}

/* ==================== MEDIA (SMALL DEVICES) ========================= */
@media screen and (max-width: 600px) {
	.about__me {
		width: 65%;
		margin: 0 auto 3rem;
	}
	.about__cards {
		grid-template-columns: 1fr;
		gap: 1rem;
	}
	.about__content {
		text-align: center;
	}
	.about__content p {
		margin: 1.5rem 0;
	}
}